<template>
    <el-dialog title="" :visible="dialogFormVisible" @close="$emit('close')">
        <el-form :model="config.form">
            <el-form-item label="()">
            <el-input v-model="config.form.rows"></el-input>
            </el-form-item>
            <el-form-item label="">
            <el-input v-model="config.form.cols"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button type="success" plain @click="$emit('close')"> </el-button>
            <el-button type="success" @click="insertTable"> </el-button>
        </div>
    </el-dialog>
</template>

<script>
import config from '../../scripts/config'
import {mapState, mapMutations} from 'vuex';
export default {
    props: {
        dialogFormVisible: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            config: config
        }
    },
    computed: {
        ...mapState({
            editor: state=> state.editor
        })
    },
    methods: {
        // 
        insertTable() {
            const cursor = this.editor.getCursor()
            const rows = parseInt(this.config.form.rows)
            const cols = parseInt(this.config.form.cols)
            if (isNaN(rows) || isNaN(cols) || rows < 1 || cols < 1) {
                this.$message({
                    showClose: true,
                    message: '/',
                    type: 'error'
                })
                return
            }

            let table = ''
            for (let i = 0; i < rows + 2; ++i) {
                for (let j = 0; j < cols + 1; ++j) {
                    table += (j === 0 ? '|' : (i !== 1 ? '     |' : ' --- |'))
                }
                table += '\n'
            }

            this.editor.replaceSelection(`\n${table}\n`, cursor)
            this.$emit('close')
            this.editorRefresh()
        },
        ...mapMutations(['editorRefresh'])
    }
}
</script>

